import { cloudFunction } from '@/utils/cloud'
import { ArticleList } from './ArticleList'

export default {
  components: { ArticleList },
  data () {
    return {
      currentTab: 0, //预设当前项的值
      scrollLeft: 0, //tab标题的滚动条位置
      loading: true,
      tagList: [],
      articleList: []
    }
  },
  async mounted () {
    try {
      const TagRes = await cloudFunction('getTagList')
      if (!TagRes[0]) this.tagList = TagRes[1].data
      // 或者首项文章
      if (this.tagList.length !== 0) {
        const id = this.tagList[0]['id']
        this.getArticleByTag(id)
      }
    } catch (err) {
      this.loading = false
    }
  },
  methods: {
    // 滚动切换标签样式
    switchTab: function (e) {
      this.currentTab = e.detail.current;
      const id = this.tagList[e.detail.current]['id']
      this.getArticleByTag(id)
      this.checkCor();
    },
    getArticleByTag (id) {
      this.loading = true
      cloudFunction('getArticleByTag', { id })
        .then((res) => {
          const [err, data] = res
          if (data.code === 0) {
            this.articleList = data.data
          }
          setTimeout(() => {
            this.loading = false
          }, 800)
        }).catch((err) => {
          this.loading = false
        })
    },
    // 点击标题切换当前页时改变样式
    swichNav: function (e) {
      let cur = e.currentTarget.dataset.current;
      if (this.currentTab == cur) {
        return false;
      } else {
        this.currentTab = cur
      }
    },
    //判断当前滚动超过一屏时，设置tab标题滚动条。
    checkCor: function () {
      if (this.currentTab > 4) {
        this.scrollLeft = 600
      } else if (this.currentTab > 2) {
        this.scrollLeft = 300
      } else {
        this.scrollLeft = 0
      }
    },
    // 前往详情页
    toDetail (article) {
      uni.navigateTo({
        url: `/pages/article/detail?title=${article.title}`
      })
    }
  }
}
